import CategoriesComp from 'components/home/CategoriesComp'
import ProductsQueryComp from 'components/home/ProductsQueryComp'
import FavoritesComp from 'components/home/FavoritesComp'
import React from 'react'
import SliderComp from 'components/home/SliderComp'

const HomePage = () => {
  return (
    <div className=''>

      <SliderComp/>
      
      <CategoriesComp/>

      <FavoritesComp store={"FavoriteReducer"} title={' '} />

      <ProductsQueryComp list={'latest'} title={' '}/>
      <ProductsQueryComp list={'review'} title={' '}/>
      <ProductsQueryComp list={'price'} title={' '}/>
      <ProductsQueryComp list={'views'} title={' '}/>
    </div>
  )
}

export default HomePage