import { z } from "zod";

export const ForgetPassValidation = z.object({
    phoneORemail: z.string()
    .min(1, "   ")
    .refine(value => {
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        const phoneRegex = /^01\d{9}$/;
        return emailRegex.test(value) || phoneRegex.test(value);
    }, {
        message: "       "
    })
});